body {
  padding: 0;
  margin: 0;
  height: 100%;
}

.App {
  
  font-family: "Microsoft YaHei","黑体","宋体",sans-serif;
  width: 1360px;
  margin: auto;
  position: relative;
  height: 100%;
}

.App .App-navbar {
  /* background: #f6f4f3; */
  min-width: 1060px;
  color: #efefef;
  text-align: center;
  padding-top: 20px;
  position: fixed;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  z-index: 200;
  width: 100%;
  left:50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
 

}

.App .App-navbar .row {
    min-width: 1060px;
}

.App .App-navbar a:hover{
  color: #3498db;
}


/*sideNav*/

.side-nav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    opacity: 0.8;
    overflow-x: hidden;
    padding-top: 80px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.side-nav a {
    padding: 10px 10px 35px 30px;
    text-decoration: none;
    font-size: 22px;
    color: #ccc;
    display: block;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}

.side-nav a:hover {
    color: #fff;
}

.side-nav .btn-close {
    position: absolute;
    top: 0;
    right: 22px;
    font-size: 36px;
    margin-left: 50px;
}

.open-slide {
    display: block;
    position: fixed;
    z-index: 10000;
    top: 10px;
    left: 10px;
}



