}

/* ResearchSlider */
.container {
    margin: auto;
    width: 90%;
    height: 50vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-bottom: 30px;
    padding: 0 20px;
}

.container .box {
    margin: auto;
    width: 15%;
    text-align: center;
    line-height: 60vh;
    -ms-flex-positive: 1;
        flex-grow: 1;
    background-size: cover;
    background-position: center;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.container .box h1 {
    margin: 0;
    padding: 0;
    line-height: 45vh;
    font-size: 2em;
    background: rgba(255, 255, 255, 0.6);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.container .box h1:hover {
    color: #262626;
    background: rgba(84, 160, 255, 0.5);
    letter-spacing: 10px;
    -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.container .box h1 span {
    background: rgba(255, 255, 255, 0);
    width: 50%;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    color: #fff;
    padding: 25px 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.container .box h1 a {
    text-decoration: none;
}

.container .box:hover h1 span {
    color: #262626;
    background: rgba(84, 160, 255, 0.5);
    letter-spacing: 10px;
    -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.container .box:hover h1 {
    background: rgba(0, 0, 0, 0);
}

.container .box:hover {
    width: 28%;
    cursor: pointer;
}

.container .box:nth-child(1) {
    background-image: url('./img/p1.png');
    background-position: left;
}

.container .box:nth-child(2) {
    background-image: url('./img/p2.png');
    background-position: left;
}

/* .container .box:nth-child(2) {
    background-image: url('./img/slider/pic3.png');
} */

.container .box:nth-child(3) {
    background-image: url('./img/p3.png');
    background-position: left;
}

.container .box:nth-child(4) {
    background-image: url('./img/p4.png');
    background-position: left;
}
.container .box:nth-child(5) {
    background-image: url('./img/p5.png');
    background-position: left;
}
.container .box:nth-child(6) {
    background-image: url('./img/p6.png');
    background-position: left;
}


